.slider {
    width: 100%;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    /* background: url("../../public/Images/fond.jpg"); */
    /* background-size: cover; */
    background-color: #04439b73;
}

.bg-head {
    background: linear-gradient(#e5ff00, #04439b5c, #ffffff);
    min-height: 1rem;
}

.s-des {
    background: url("../../public/Images/fond.jpg");
    background-size: cover;
}

.slide-l {
    min-height: 80vh;
}

.contents {
    transition: transform ease-in-out 1s;
}

.s-content {
    position: relative;
    display: flex;
    width: 100%;
    flex: 1 0 100%;
    justify-content: center;
    /* height: 350px; */
}

.s-right,
.s-left {
    position: absolute;
    top: 50%;
    font-size: 2rem;
    transform: translateY(-50%);
    cursor: pointer;
    color: #ffffff;
}

.s-left {
    left: 20px;
}

.s-right {
    right: 20px;
}

.s-content img {
    max-height: 250px;
}

.s-content div {
    margin: 1.5rem 0;
}

.s-des {
    left: 10%;
}

.my-name {
    font-size: 5rem;
}

.phoenix-coder {
    padding-top: 2rem;
    z-index: -1;
}